import { Component, OnInit } from '@angular/core';
import { validateConfig } from '@angular/router/src/config';
import { HtmlTagDefinition } from '@angular/compiler';
declare var $;
@Component({
  selector: 'app-faq',
  templateUrl: './faq.component.html',
  styleUrls: ['./faq.component.css']
})
export class FaqComponent implements OnInit {

  constructor() { }

  ngOnInit() {
  }

search()
{
 

  var faq : HTMLElement ;
  (<any>faq) = document.getElementsByClassName("title") ;
var box : HTMLElement;
(<any>box) = document.getElementsByClassName("card-block-inner") ;






 var input = $("#searchMobileOne").val();

  var filter = input.toUpperCase();


  $(".card-block-inner").css("display","none");

  for (var i = 0; i < (<any>faq).length; i++) {
      

    if ( faq[i].innerText.toUpperCase().indexOf(filter) > -1) 
    {
       
       box[i].style.display = "block"
        console.warn("same")
      
        // break;
        // console.log("Ok")
    } else {
      box[i].style.display = "none"
        console.warn("Not same")
      
    }
}



  // $(".card-block-inner").css("display","block");






  
}



}
